<template>
  <div class="category-list">
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="240"></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="desc" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="150">
        <template slot-scope="scope">
          <!-- <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button> -->
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/guides/edit/${scope.row._id}`)"
          ></el-button>
          <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      items: []
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("rest/guides");
      this.items = res.data;
    },
    remove(row) {
      this.$confirm(` "${row.title}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(async () => {
        const res = await this.$http.delete(`rest/guides/${row._id}`);
        if (res.data.success) {
          this.$message({
            type: "success",
            message: "!"
          });
          this.fetch()
        } else {
          this.$message({
            type: "faild",
            message: "!"
          });
        }
      });
    }
  },
  created() {
    this.fetch();
  }
};
</script>
